@tailwind base;
@tailwind components;
html {
  @apply bg-zinc-800 text-gray-200  mx-auto font-["Poppins"] scroll-smooth;
}
@layer utilities {
  .no-scrollbar::-webkit-scrollbar {
    display: none;
  }
  .no-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
}
